import React from 'react'
import { 
    ADMIN_ROUTE, 
    PROFILE_ROUTE,
    PROJECTS_ROUTE,
    COMPANYS_ROUTE,
    SPECIALIST_ROUTE,
    PLATFORMS_ROUTE,

} from "./utils/consts";

const Admin = React.lazy(() => import('./pages/Admin'))
const Profile = React.lazy(() => import('./pages/Profile'))
const Projects = React.lazy(() => import('./pages/Projects'))

const Specialist = React.lazy(() => import('./pages/Specialist'))
const Companys = React.lazy(() => import('./pages/Companys'))
const Platforms = React.lazy(() => import('./pages/Platforms'))

export const authRoutes = [
    { path: ADMIN_ROUTE, name: ' ', Component: Admin },
    { path: PROFILE_ROUTE, name: '', Component: Profile },
    { path: SPECIALIST_ROUTE, name: '', Component: Specialist },
    { path: COMPANYS_ROUTE, name: '', Component: Companys },
    { path: PROJECTS_ROUTE, name: '', Component: Projects },
    { path: PLATFORMS_ROUTE, name: '', Component: Platforms },

    { path: '/', name: ' ', Component: Admin },
]
